*{
    margin: 0;
    padding: 0;

    box-sizing: border-box;
    font-size: 10;
    font-weight: 100;

    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;

}


$height-header-sec-px :60px;

html{
    body{
       height: 100vh;
    //    padding-top: $height-header-sec-px;
    }
}

#header-sec{
    // position: fixed;
    z-index: 1;
    top: 0;
    width: 100%;
    // height: $height-header-sec-px;
    // height: fit-content;

    background-color: red;

    .page-scroll-bar{
        width: 100%;
        height: 10px;
        progress{
            
            display: block;
            height: 100%;
            width: 100%;
            appearance: none;
            border: none;
            outline: none;
            $porg-bar:rgb(0, 0, 0);
            $porg-val: rgb(31, 153, 33);


            background-color:$porg-bar;//work on mozila 
            &::-webkit-progress-bar {
                /* style rules */
                background-color:$porg-bar;
            }
            &::-webkit-progress-value {
                /* style rules */
                background-color:  $porg-val;
            }
            &::-moz-progress-bar {
                /* style rules */
                background-color:$porg-val;
              }
        }
    }
    .nav-bar{
        height: fit-content;
        display: flex;
        justify-content: space-evenly;
        flex-wrap: wrap;
        padding-block: 10px;
        $gap-padingLeft :20px;
        gap: $gap-padingLeft;
        padding-inline: $gap-padingLeft;
        /*gap will not efict the wall*/

        align-items: center;
        .greetings,.topic-btn .aTag{
            font-size: 2rem;
            font-weight: 700;
            color: white;
        }
        .greetings{
            margin-right: auto;
        }

        .topic-btn{
            .aTag{
            
                display: block;
                cursor: pointer;
                background-color: black;
                padding: 5px 10px ;
                &:hover{
                    transform: translateX(2px) translateY(-2px);
                    box-shadow: -2px 2px 10px 3px rgba(255, 255, 255, 0.654);
                }
            }
           
        }
    }
}


#TecSkills-sec-cards{
    margin: auto;
    width: 100%;
    display: grid;
    justify-content: center;

    grid-template-columns: repeat( auto-fit, minmax(30px, 80px) );

    .tech-card{
        cursor: pointer;
        overflow: hidden;
        background-color: rgb(255, 255, 255);
        aspect-ratio: 1;
        


        &:hover{
            .tech-name{
          
                transform: translateY(0%);
    
            }
        }
        .tech-img{
            user-select: none;
            pointer-events: none;
            height: 100%;
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            img{

                width: 80%;

                object-fit: contain;
                object-position: center;
            }
        }

        .tech-name{
            transition: transform .2s;
    
            transform: translateY(-100%);
            
            background-color: rgba(0, 0, 0, 0.913);
            // backdrop-filter: blur(3px);/*cause performans issues*/
            height: 100%;
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            font-weight: 900;
            font-size: 14px;
            user-select: none;
            // word-wrap: break-word;
            word-break: break-all;
            color: white;
        }
    }
}

